.catalog__card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 198px;
  height: 406px;
  transition: transform .3s;
  display: flex;
}

.catalog__card-text {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.catalog__card:hover .catalog__card-text {
  color: #34568b;
  transition: color .3s;
}

.catalog__card-image {
  box-sizing: border-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.catalog__card-content {
  padding: 40px 16px 16px;
}

.catalog__card-code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
}

.stars {
  justify-content: space-between;
  padding-top: 18px;
  padding-left: 2px;
  display: flex;
}

.stars__star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 12px;
  height: 12px;
}

.stars__reviews {
  color: #060b35;
  margin-left: 18px;
  font-size: 10px;
  font-weight: 400;
}

.stars--1 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog__card-price {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  display: flex;
}

.catalog__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.catalog__price-money {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.catalog__card-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transform: translateY(16px);
}

.catalog__card-button:hover {
  color: #00acdc;
  background-color: #fff;
}
